<template>
    <div id="loader">
        <svg v-for="n in 4" :class='"anim delay" + n + " " + color(n)' 
           xmlns:dc="http://purl.org/dc/elements/1.1/" xmlns:cc="http://creativecommons.org/ns#"
           xmlns:rdf="http://www.w3.org/1999/02/22-rdf-syntax-ns#"
           xmlns:svg="http://www.w3.org/2000/svg"
           xmlns="http://www.w3.org/2000/svg"
           width="88.733482mm"
           height="88.733482mm"
           viewBox="0 0 88.733482 88.733482"
           version="1.1"
           id="svg8"
           >
          <defs
             id="defs2" />
          <metadata
             id="metadata5">
            <rdf:RDF>
              <cc:Work
                 rdf:about="">
                <dc:format>image/svg+xml</dc:format>
                <dc:type
                   rdf:resource="http://purl.org/dc/dcmitype/StillImage" />
                <dc:title></dc:title>
              </cc:Work>
            </rdf:RDF>
          </metadata>
          <g
             id="layer1"
             transform="translate(-37.417736,-43.628483)">
            <circle
               style="opacity:1;vector-effect:none;fill:none;fill-opacity:1;stroke-linejoin:miter;stroke-miterlimit:4;stroke-opacity:1"
               id="path815"
               cx="81.784477"
               cy="87.995224"
               r="44.366741" />
          </g>
        </svg>
      <img src="../assets/godot_logo.svg"/>
    </div>
</template>

<script>
export default {
  props: ['id'],
  name: 'circle-spinner',
  data () {
    return {
      colors: ['blue', 'green', 'red', 'darkblue']
    }
  },
  methods: {
    color (index) {
      let color = this.colors[index % this.colors.length]
      return color
     // return this.rgbToHex([index * 2, index * 2, index])
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
$colors: (blue: #478CBF, lightblue: #56a7e4, lighterblue: #afd5f2, lightestblue: #7dc0f2);
@each $color, $hex in $colors {
  .#{$color} {
    stroke: $hex;
  }
}

#loader {
  position: relative;
  width: 200px;
  height: 200px;
  display: flex;
  justify-content: center;
  background-color: white;
  border: 10px solid white;
  border-radius: 100%;
}
svg {
  width: 100%;
  height: 100%;
  border-radius: 100%;
  position: absolute;
  stroke-width: 10px;
  stroke-dasharray: 50;
}
.anim {
  animation-duration: 3s;
  animation-name: rotate, stroke;
  animation-iteration-count: infinite, infinite;
  animation-timing-function: ease, cubic-bezier(.45,.05,.55,.95);
  stroke-dasharray: 320px;
}

@for $i from 2 through 4 {
    .delay#{$i} {
      animation-delay: #{$i/4}s;
    }
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(3turn);
  }
}
@keyframes stroke {
  from {
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dashoffset: 300;
  }
  to {
    stroke-dashoffset: 0;
  }
}

.circle {
  display: none;
  width: 16px;
  height: 16px;
}
</style>




